import Vue from 'vue'
import Router from 'vue-router'
import Dtfd from '@/views/Dtfd'
import Zhzh from '@/views/Zhzh'
import Zbzy from '@/views/Zbzy'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/dtfd',
      name: 'dtfd',
      component: Dtfd
    },
    {
      path: '/zhzh',
      name: 'zhzh',
      component: Zhzh
    },
    {
      path: '/zbzy',
      name: 'zbzy',
      component: Zbzy
    },
    {
      path: '*',
      redirect: '/dtfd'
    }
  ]
})



