<template>
  <div class="left-wrap">
    <el-form :model="form" :rules="rules" ref="ruleForm" label-position="top" label-width="80px">

      <el-form-item label="图层" prop="layer">
        <el-select v-model="form.layer" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="纬度" prop="latitude">
        <el-input placeholder="如: 12.5" v-model="form.latitude"></el-input>
      </el-form-item>

      <el-form-item label="经度" prop="longitude">
        <el-input placeholder="如: 12.5" v-model="form.longitude"></el-input>
      </el-form-item>

      <el-form-item label="距离">
        <el-slider v-model="form.distance" :max="10000" :min="500" :step="100"></el-slider>
      </el-form-item>

      <el-form-item class="button-wrap">
        <el-button type="primary" @click="handleSearch()">查询</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {Loading} from 'element-ui'
  import * as api from '../store/api'

  export default {
    name: 'zbzy',
    data () {
      return {
        list: '',
        options: [
          {
            label: '出入口',
            value: 'gl_crk'
          },
          {
            label: '桥梁',
            value: 'gl_ql'
          }
        ],
        form: {
          layer: 'gl_ql',
          latitude: '25.830016530747336',
          longitude: '114.7703760406708',
          distance: 500
        },
        rules: {
          layer: [
            {required: true, message: '请选择图层名称', trigger: 'change'}
          ],
          latitude: [
            {required: true, message: '请输入纬度', trigger: 'change'}
          ],
          longitude: [
            {required: true, message: '请输入经度', trigger: 'change'}
          ]
        }
      }
    },
    methods: {
      handleSearch () {
        let loadingService = Loading.service({fullscreen: true})
        api.zbzy(this.form).then(res => {
          this.list = res.data.data
          loadingService.close()
          this.$message({
            message: '查询成功！',
            type: 'success'
          })
        }).catch(error => {
          console.log(error)
          loadingService.close()
          this.$message.error('查询出错！')
        })
      }
    }
  }
</script>

<style lang="scss">
</style>



