<template>
  <div class="left-wrap">
    <div class="server-wrap">
      <span>服务切换</span>
      <el-radio-group v-model="server">
        <el-radio-button label="node"></el-radio-button>
        <el-radio-button label="sde"></el-radio-button>
      </el-radio-group>
    </div>
    <el-form :model="form" :rules="rules" ref="ruleForm" label-position="top" label-width="80px">

      <el-form-item label="路线名称" prop="lxbm">
        <el-autocomplete v-model="form.lxbm"
                         popper-class="my-autocomplete"
                         custom-item="my-item-zh"
                         placeholder="如: G15"
                         :fetch-suggestions="querySearchAsync"
                         @select="handleSelect"></el-autocomplete>
      </el-form-item>

      <el-form-item label="路线方向" prop="sxxfx">
        <el-select v-model="form.sxxfx" placeholder="请选择方向">
          <el-option label="上行" value="1"></el-option>
          <el-option label="下行" value="2"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-card class="box-card">
          <div class="text item">
            桩号范围:
          </div>
          <div class="text item">
            {{qdzh}} - {{zdzh}}
          </div>
        </el-card>
      </el-form-item>

      <el-form-item label="桩号" prop="zh">
        <el-input placeholder="如: 12.5" v-model="form.zh">
          <el-button slot="append" @click="handleZh()">转换</el-button>
        </el-input>
      </el-form-item>

      <el-form-item>
        <el-card class="box-card">
          <div class="text item">
            node 经纬度:
          </div>
          <div class="text item">
            {{nodeCoord.geometry}}
          </div>
          <div class="text item">
            sde 经纬度:
          </div>
          <div class="text item">
            {{sdeCoord.geometry}}
          </div>
        </el-card>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
  import Vue from 'vue'
  import {Loading} from 'element-ui'
  import * as api from '../store/api'
  Vue.component('my-item-zh', {
    functional: true,
    render: function (h, ctx) {
      var item = ctx.props.item
      return h('li', ctx.data, [
        h('div', {attrs: {class: 'one'}}, [item.code]),
        h('span', {attrs: {class: 'two'}}, [item.name])
      ])
    },
    props: {
      item: {type: Object, required: true}
    }
  })

  var timer = null

  export default {
    name: 'zhzh',
    data () {
      return {
        qdzh: 0,
        zdzh: 0,
        nodeCoord: '',
        sdeCoord: '',
        server: 'node',
        form: {
          lxbm: '',
          sxxfx: '',
          zh: ''
        },
        rules: {
          lxbm: [
            {required: true, message: '请输入路线名称', trigger: 'change'}
          ],
          sxxfx: [
            {required: true, message: '请选择路线方向', trigger: 'change'}
          ],
          zh: [
            {required: true, message: '请输入桩号', trigger: 'change'}
          ]
        }
      }
    },
    methods: {
      querySearchAsync (queryString, cb) {
        clearTimeout(timer)
        timer = setTimeout(() => {
          api.auto({
            lxbm: queryString
          }).then(res => {
            cb(res.data.data)
          })
        }, 500)
      },
      handleSelect (item) {
        console.log(item)
        this.form.lxbm = item.code
        this.qdzh = item.qdzh
        this.zdzh = item.zdzh
      },
      handleZh () {
        this[this.server + 'Server']()
      },
      nodeServer () {
        let loadingService = Loading.service({fullscreen: true})
        api.zhzh(this.form).then(res => {
          let data = res.data.data
          data.attributes.style = {
            icon: {
              imageSrc: '../../static/images/marker-50.png'
            }
          }
          this.nodeCoord = data
          this.marker(this.nodeCoord, 'node')
          loadingService.close()
          this.$message({
            message: '查询成功！',
            type: 'success'
          })
        }).catch(error => {
          console.log(error)
          loadingService.close()
          this.$message.error('查询出错！')
        })
      },
      sdeServer () {
        let loadingService = Loading.service({fullscreen: true})
        api.sdeZhzzb({
          layerName: 'gis_luxian',
          filter: 'LXBM=\'' + this.form.lxbm + '\' AND ' + 'SXXFX=' + this.form.sxxfx,
          m: this.form.zh
        }).then(res => {
          let data = res.data.data
          data.geometry = [data.lon, data.lat]
          this.sdeCoord = data
          this.marker(this.sdeCoord, 'sde')
          loadingService.close()
          this.$message({
            message: '查询成功！',
            type: 'success'
          })
        }).catch(error => {
          console.log(error)
          loadingService.close()
          this.$message.error('查询出错！')
        })
      },
      marker (data, layer) {
        window.Maps.removeFeatureByLayerName(layer + 'Marker')
        window.Maps.addPoint(data, {
          layerName: layer + 'Marker'
        })
      }
    }
  }
</script>

<style lang="scss">
</style>



