<template>
  <div id="app">
    <header class="header-wrap">
      <img src="../static/pig.png" alt="pig">地图服务
    </header>
    <div class="content-wrap">

      <div class="content">

        <div class="nav">
          <el-menu default-active="0" class="el-menu-vertical-demo" theme="dark">
            <el-menu-item :index="index + ''" v-for="(item, index) in nav" :key="item.title" @click="goto(item)">{{item.title}}</el-menu-item>
          </el-menu>
        </div>

        <div class="view">
          <el-scrollbar
            tag="div"
            class="view-scrollbar"
            wrap-class="el-select-dropdown__wrap"
            view-class="el-select-dropdown__list">
            <router-view></router-view>
          </el-scrollbar>
        </div>

        <div class="map">
          <div id="map"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import router from './router'
  export default {
    name: 'app',
    data () {
      return {
        nav: [
          {
            title: '动态分段',
            name: 'dtfd'
          },
          {
            title: '桩号转换',
            name: 'zhzh'
          },
          {
            title: '周边资源',
            name: 'zbzy'
          }
        ]
      }
    },
    mounted () {
      let Maps = new HMap.Map()
      Maps.initMap('map', {
        view: {
          center: [108.95079717047562, 35.48836545260525],
          projection: 'EPSG:4326',
          zoom: 5
        },
        baseLayers: [
          {
            layerName: 'openstreetmap',
            isDefault: true,
            layerType: 'OSM',
            opaque: true, // 图层是否不透明
            layerUrl: 'https://{a-c}.tile.openstreetmap.org/{z}/{x}/{y}.png'
          }
        ]
      })
      window.Maps = Maps
    },
    methods: {
      goto (item) {
        router.push({name: item.name})
      }
    }
  }
</script>

<style lang="scss">
  html, body {
    height: 100%;
  }

  ul, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .view-scrollbar {
    height: 100%;
    > .el-select-dropdown__wrap {
      height: 100%;
      max-height: 100%;
      overflow-x: hidden;
    }
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    position: relative;
    height: 100%;
    width: 100%;
  }

  .header-wrap {
    height: 50px;
    line-height: 50px;
    background-color: #20a0ff;
    padding: 0 20px;
    color: #fff;
    font-size: 24px;
    img {
      float: left;
      height: 30px;
      margin: 10px 10px 0 0;
    }
  }
  .content-wrap {
    position: absolute;
    top: 50px;
    bottom: 0;
    width: 100%;
    .content {
      height: 100%;
      width: 400px;
      float: left;
      .nav {
        height: 100%;
        width: 100px;
        background-color: #324157;
      }
      .view {
        position: absolute;
        left: 100px;
        top: 0;
        height: 100%;
        width: 300px;
        background-color: #fafafa;
      }
      .map {
        position: absolute;
        top: 0;
        left: 400px;
        right: 0;height: 100%;
      }
      #map {
        width: 100%;
        height: 100%;
      }
    }
    .map{
      width: calc(100% - 400px);
      height: 100%;
      float: left;
    }
  }

  .left-wrap {
    padding: 20px;
    .my-autocomplete {
      li {
        line-height: normal;
        padding: 7px;

        .one {
          text-overflow: ellipsis;
          overflow: hidden;
        }
        .two {
          font-size: 12px;
          color: #b4b4b4;
        }

        .highlighted .two {
          color: #ddd;
        }
      }
    }
    .el-autocomplete, .el-select {
      width: 100%;
    }
    .button-wrap {
      text-align: center;
    }
    .server-wrap {
      > span {
        display: inline-block;
        font-size: 14px;
        color: #48576a;
        line-height: 1;
        padding: 0 0 10px;
      }
      .el-radio-group {
        width: 100%;
        margin-bottom: 30px;
        label {
          width: 50%;
          span {
            width: 100%;
          }
        }
      }
    }
  }
</style>



